import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm, formValueSelector, reset } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { renderField, renderFieldSelect } from "../../renderField";
import Skeleton from "react-loading-skeleton";
import { getListBarang } from "../../actions/kodeBarangAction";
import {
  calculate_barcode_level2,
  calculate_barcode_level3,
  setFocus,
} from "../../helper";
import { AxiosMasterGet } from "../../Axios";
import Swal from "sweetalert2";
import { getListTransaksiPenjualan } from "../../actions/transaksiPenjualanAction";
import { getTotalNW } from "../../actions/returnPenjualanAction";
import { gagal } from "../../Alert";
import { getNetto } from "../../actions/LogicAction";
import { hideModal } from "../../actions/ModalAction";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});

class FormBarangSet extends Component {
  constructor(props) {
    super(props);
    this.state = {
      kodeBarang: [],
      non_aksesoris: "col-lg-12 row",
      aksesoris: "col-lg-12 row d-none",
    };
  }

  componentDidMount() {
    this.props.dispatch(getListBarang());
    // getTimbangan(this.props.change);
  }

  onclick1() {
    this.props.change("netto", this.props.netto);
    this.props.change("noFaktur", this.props.noFaktur);
    this.props.change("gross", this.props.gross);
    this.props.change("discGlobalGr", this.props.discGlobalGr);
    this.props.change("nettoDiscGlobalExt", this.props.netto);
  }

  setKadar() {
    this.props.change("keterangan", this.state.namaJenis);
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarTransaksi", this.state.kadarJual);
    this.props.change("kodeKategori", this.state.kodeKategori);
    this.props.change("namaKategori", this.state.namaKategori);
    this.props.change("harga_jual", this.state.harga_jual);
  }

  setBruto() {
    this.props.change("bruto", localStorage.getItem("Timbangan"));
  }

  handleBarcode = () => (e) => {
    clearTimeout(this.delay);
    this.delay = setTimeout(() => {
      let hasil = e.target.value;
      if (hasil.length === 14) {
        AxiosMasterGet(
          "barang-set/get-by-kode-barcode-set-active/" + hasil
        ).then((res) => {
          let total_berat_set = res.data.detail_barang
            .map((data) => parseFloat(data.netto))
            .reduce((a, b) => a + b, 0);
          res.data.detail_barang.forEach((data) => {
            this.handleBarcodeSet(
              data,
              res.data.kode_barcode_set,
              res.data.detail_barang.length,
              total_berat_set,
              data.harga_jual,
              data.qty
            );
          });
        });
        return true;
      } else {
        let split = hasil.split("X");
        this.props.change("kodeBarang", split[0]);
        this.setState({
          kodeJenis: split[0],
          kadarCetak: split[1],
          kadarJual: split[2],
        });
      }
    }, 1500);
  };

  async handleBarcodeSet(
    data,
    barcode,
    jumlah_barang,
    total_berat_set,
    harga_jual,
    qty
  ) {
    console.log(data);
    let param = data.kode_jenis;
    let hasil = this.props.KodeBarangList.filter(
      (data) => data.kode_jenis === param
    );
    let kode_tukar_2 = 0;
    let kode_tukar_3 = 0;
    let customer_2 =
      this.props.level_customer === "LEVEL2"
        ? this.props.kodeCustomer
        : localStorage.getItem("customer_2");
    let customer_3 =
      this.props.level_customer === "LEVEL3" ? this.props.kodeCustomer : "-";
    await AxiosMasterGet(
      `/kelompok-harga/get/by-jenis-and-customer/${customer_2}&${data.kode_jenis}`
    )
      .then((res) => (kode_tukar_2 = res.data.kode_tukar_normal || 0))
      .catch(() => (kode_tukar_2 = 0));
    await AxiosMasterGet(
      `/kelompok-harga/get/by-jenis-and-customer/${customer_3}&${data.kode_jenis}`
    )
      .then((res) => (kode_tukar_3 = res.data.kode_tukar_normal || 0))
      .catch(() => (kode_tukar_3 = 0));
    AxiosMasterGet(`jenis/get/by-kode-jenis/${data.kode_jenis}`)
      .then((res) => {
        var simpan_nota_1 =
          JSON.parse(localStorage.getItem("nota_penjualan_level1")) || [];
        var simpan_nota_2 =
          JSON.parse(localStorage.getItem("nota_penjualan_level2")) || [];
        var simpan_nota_3 =
          JSON.parse(localStorage.getItem("nota_penjualan_level3")) || [];
        var nota_gambar = JSON.parse(localStorage.getItem("nota_gambar")) || [];
        var posisi = simpan_nota_1.findIndex(
          (data) => data.kode_kategori === data.kodeKategori
        );
        var posisi_2 = simpan_nota_2.findIndex(
          (data) => data.kode_kategori === data.kodeKategori
        );
        var posisi_3 = simpan_nota_3.findIndex(
          (data) => data.kode_kategori === data.kodeKategori
        );
        nota_gambar.push({ lokasi_gambar: res.data?.image });
        localStorage.setItem("nota_gambar", JSON.stringify(nota_gambar));
        if (posisi !== -1) {
          var nota_1 = {
            netto_disc: (
              parseFloat(data.netto_disc || 0) +
              parseFloat(simpan_nota_1[posisi].netto_disc)
            ).toFixed(3),
            netto: (
              parseFloat(data.netto || 0) +
              parseFloat(simpan_nota_1[posisi].netto)
            ).toFixed(3),
            ongkos_rp: (
              parseFloat(data.rp === undefined ? 0 : data.rp) +
              simpan_nota_1[posisi].ongkos_rp
            ).toLocaleString("id-ID"), //ongkos
            disc: (
              parseFloat(data.disc_persen || 0) +
              parseFloat(simpan_nota_1[posisi].disc)
            ).toFixed(3),
            bruto: (
              parseFloat(data.bruto || 0) +
              parseFloat(simpan_nota_1[posisi].bruto)
            ).toFixed(3), //berat
            kode_kategori: data.kodeKategori,
            kode_tukar: data.kadarTransaksi,
            keterangan: data.keterangan,
            jumlah_barang_set: jumlah_barang,
            total_berat_barang_set: total_berat_set,
            kode_barcode_set: barcode,
            harga_jual:
              parseFloat(harga_jual) * parseFloat(qty) +
              simpan_nota_1[posisi].harga_jual,
          };
          var nota_2 = {
            netto_disc: (
              (parseFloat(data.bruto || 0) * parseFloat(kode_tukar_2)) / 100 -
              (parseFloat(data.disc_persen) * parseFloat(data.bruto || 0)) /
                100 +
              parseFloat(simpan_nota_2[posisi].netto_disc)
            ).toFixed(3),
            netto: (
              ((parseFloat(data.bruto || 0) * parseFloat(kode_tukar_2)) / 100 ||
                0) + parseFloat(simpan_nota_2[posisi].netto)
            ).toFixed(3), //murni
            ongkos_rp:
              parseFloat(data.rp === undefined ? 0 : data.rp) +
              parseFloat(simpan_nota_2[posisi].ongkos_rp), //ongkos
            disc: (
              (parseFloat(data.disc_persen) / 100) *
                parseFloat(data.bruto || 0) +
              parseFloat(simpan_nota_2[posisi].disc)
            ).toFixed(3),
            bruto:
              parseFloat(data.bruto || 0) +
              parseFloat(simpan_nota_2[posisi].bruto), //berat
            kode_kategori: data.kodeKategori,
            kode_tukar: kode_tukar_2,
            keterangan: data.keterangan,
            jumlah_barang_set: jumlah_barang,
            total_berat_barang_set: total_berat_set,
            kode_barcode_set: barcode,
            harga_jual:
              parseFloat(harga_jual) * parseFloat(qty) +
              simpan_nota_2[posisi].harga_jual,
          };
          var nota_3 = {
            netto_disc: (
              (parseFloat(data.bruto || 0) * parseFloat(kode_tukar_3)) / 100 -
              (parseFloat(data.disc_persen) * parseFloat(data.bruto || 0)) /
                100 +
              parseFloat(simpan_nota_3[posisi].netto_disc)
            ).toFixed(3),
            netto: (
              ((parseFloat(data.bruto || 0) * parseFloat(kode_tukar_3)) / 100 ||
                0) + parseFloat(simpan_nota_3[posisi].netto)
            ).toFixed(3), //murni
            ongkos_rp:
              parseFloat(data.rp === undefined ? 0 : data.rp) +
              parseFloat(simpan_nota_3[posisi].ongkos_rp), //ongkos
            disc: (
              (parseFloat(data.disc_persen) / 100) *
                parseFloat(data.bruto || 0) +
              parseFloat(simpan_nota_3[posisi].disc)
            ).toFixed(3),
            bruto:
              parseFloat(data.bruto || 0) +
              parseFloat(simpan_nota_3[posisi].bruto), //berat
            kode_kategori: data.kodeKategori,
            kode_tukar: kode_tukar_3,
            keterangan: data.keterangan,
            jumlah_barang_set: jumlah_barang,
            total_berat_barang_set: total_berat_set,
            kode_barcode_set: barcode,
            harga_jual:
              parseFloat(harga_jual) * parseFloat(qty) +
              +simpan_nota_3[posisi].harga_jual,
          };
          simpan_nota_1.push(nota_1);
          simpan_nota_2.push(nota_2);
          simpan_nota_3.push(nota_3);
          simpan_nota_1.splice(posisi, 1);
          simpan_nota_2.splice(posisi_2, 1);
          simpan_nota_3.splice(posisi_3, 1);
          localStorage.setItem(
            "nota_penjualan_level1",
            JSON.stringify(simpan_nota_1)
          );
          localStorage.setItem(
            "nota_penjualan_level2",
            JSON.stringify(simpan_nota_2)
          );
          localStorage.setItem(
            "nota_penjualan_level3",
            JSON.stringify(simpan_nota_3)
          );
        } else {
          var nota_1_else = {
            netto_disc: parseFloat(data.netto_disc || 0).toFixed(3),
            netto: parseFloat(data.netto || 0).toFixed(3), //murni
            ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp), //ongkos
            disc: data.discGlobalGr || 0,
            bruto: parseFloat(data.bruto || 0).toFixed(3), //berat
            kode_kategori: data.kodeKategori,
            kode_tukar: data.kadarTransaksi,
            disc_persen: data.disc_persen || 0,
            keterangan: data.keterangan,
            jumlah_barang_set: jumlah_barang,
            total_berat_barang_set: total_berat_set,
            kode_barcode_set: barcode,
            harga_jual: parseFloat(harga_jual) * parseFloat(qty),
          };
          var nota_2_else = {
            netto_disc: (
              (parseFloat(data.bruto || 0) * parseFloat(kode_tukar_2)) / 100 -
              (parseFloat(data.disc_persen) * parseFloat(data.bruto || 0)) / 100
            ).toFixed(3),
            netto: (
              (parseFloat(data.bruto || 0) * parseFloat(kode_tukar_2)) / 100 ||
              0
            ).toFixed(3), //murni
            ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp), //ongkos
            disc: (
              (parseFloat(data.disc_persen) / 100) *
              parseFloat(data.bruto || 0)
            ).toFixed(3),
            bruto: parseFloat(data.bruto || 0), //berat
            kode_kategori: data.kodeKategori,
            kode_tukar: kode_tukar_2,
            keterangan: data.keterangan,
            jumlah_barang_set: jumlah_barang,
            total_berat_barang_set: total_berat_set,
            kode_barcode_set: barcode,
            harga_jual: parseFloat(harga_jual) * parseFloat(qty),
          };
          var nota_3_else = {
            netto_disc: (
              (parseFloat(data.bruto || 0) * parseFloat(kode_tukar_3)) / 100 -
              (parseFloat(data.disc_persen) * parseFloat(data.bruto || 0)) / 100
            ).toFixed(3),
            netto: (
              (parseFloat(data.bruto || 0) * parseFloat(kode_tukar_3)) / 100 ||
              0
            ).toFixed(3), //murni
            ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp), //ongkos
            disc: (
              (parseFloat(data.disc_persen) / 100) *
              parseFloat(data.bruto || 0)
            ).toFixed(3),
            bruto: parseFloat(data.bruto || 0), //berat
            kode_kategori: data.kodeKategori,
            kode_tukar: kode_tukar_3,
            keterangan: data.keterangan,
            jumlah_barang_set: jumlah_barang,
            total_berat_barang_set: total_berat_set,
            kode_barcode_set: barcode,
            harga_jual: parseFloat(harga_jual) * parseFloat(qty),
          };
          simpan_nota_1.push(nota_1_else);
          simpan_nota_2.push(nota_2_else);
          simpan_nota_3.push(nota_3_else);
          localStorage.setItem(
            "nota_penjualan_level1",
            JSON.stringify(simpan_nota_1)
          );
          localStorage.setItem(
            "nota_penjualan_level2",
            JSON.stringify(simpan_nota_2)
          );
          localStorage.setItem(
            "nota_penjualan_level3",
            JSON.stringify(simpan_nota_3)
          );
        }
        var simpan = {
          netto_disc: parseFloat(data.netto_disc || 0).toFixed(3) || 0,
          gross: parseFloat(data.gross || 0).toFixed(3) || 0, //berat + bungkus
          netto: parseFloat(data.netto || 0).toFixed(3) || 0, //murni
          qty: qty || 1,
          k: parseFloat(data.k || 0),
          ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp), //ongkos
          kadar_cetak: (hasil[0].kadar_cetak || 0).toString(),
          disc_persen: data.disc_persen || 0,
          kadar_jual: parseFloat(data.kadar_jual || 0), //harga%
          kadar_level2: res.data.level2 || 0,
          kadar_level3: res.data.level3 || 0,
          kadar_modal: hasil[0].kadar_modal || 0, //harga%
          kode_jenis: hasil[0].kode_jenis || 0, //kodebarang
          kode_barcode: "-",
          kode_barcode_set: barcode,
          p: parseFloat(data.p || 0),
          disc: data.disc || 0,
          pkg: parseFloat(data.pkg || 0),
          bruto: parseFloat(data.bruto || 0).toFixed(3), //berat
          harga_jual: harga_jual,
        };
        var simpan_level2 = calculate_barcode_level2({
          data: data,
          hasil: hasil,
          res: res,
          harga_jual: harga_jual,
        });
        var simpan_level3 = calculate_barcode_level3({
          data: data,
          res: res,
          hasil: hasil,
          harga_jual: harga_jual,
        });

        var array =
          JSON.parse(localStorage.getItem("transaksiPenjualan")) || [];
        var array_level2 =
          JSON.parse(localStorage.getItem("transaksiPenjualan_level2")) || [];
        var array_level3 =
          JSON.parse(localStorage.getItem("transaksiPenjualan_level3")) || [];
        array.push(simpan);
        array_level2.push(simpan_level2);
        array_level3.push(simpan_level3);
        var GrandTotalNetto = array
          .map((list) => parseFloat(list.netto_disc))
          .reduce((a, b) => a + b, 0);
        localStorage.setItem("GrandTotalNetto", GrandTotalNetto);
        localStorage.setItem("transaksiPenjualan", JSON.stringify(array));
        localStorage.setItem(
          "transaksiPenjualan_level2",
          JSON.stringify(array_level2)
        );
        localStorage.setItem(
          "transaksiPenjualan_level3",
          JSON.stringify(array_level3)
        );
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        });
        this.props.dispatch(getListTransaksiPenjualan());
        this.props.dispatch(getTotalNW());
        this.props.dispatch(reset("FormBarangSet"));
        this.props.dispatch(getNetto("transaksiPenjualan"));
        setFocus("discGlobal_penjualan");
        this.props.dispatch(hideModal());
        this.setState({
          discGlobalGr:
            (parseFloat(0) / 100) *
            parseFloat(localStorage.getItem("GrandTotalNetto")),
          discGlobalPr: 0,
          GrandTotalNetto: (
            parseFloat(localStorage.getItem("GrandTotalNetto")) - 0
          ).toFixed(3),
        });
      })
      .catch((err) =>
        gagal("Kode Kelompok Belum Disesuaikan, Mohon Sesuaikan Dahulu")
      );
  }
  setBarang(data) {
    const filtered = this.props.KodeBarangList.filter(
      (fil) => fil.kode_jenis === data
    );
    console.log(filtered);
    if (filtered[0]?.kode_kelompok === "AKSESORIS") {
      this.setState({
        non_aksesoris: "col-lg-12 row d-none",
        aksesoris: "col-lg-12 row",
      });
    } else {
      this.setState({
        non_aksesoris: "col-lg-12 row",
        aksesoris: "col-lg-12 row d-none",
      });
    }
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-12 col-lg-12 d-none">
            <Field
              name="noFaktur"
              label={this.props.noFaktur}
              type="text"
              readOnly={true}
              component={renderField}
              defaultValue={this.props.noFaktur}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-4 col-lg-4">
            {String(this.props.KodeBarangList) !== "" ? (
              <Field
                name="kodeBarang"
                label="Kode Model"
                component={renderFieldSelect}
                className="form-control"
                onBlur={this.setKadar()}
                onChange={(data) => this.setBarang(data)}
                options={this.props.KodeBarangList.filter(
                  (data) =>
                    data.kode_kelompok === "PERHIASAN" ||
                    data.kode_kelompok === "AKSESORIS"
                ).map((kode) => {
                  let data = {
                    value: kode.kode_jenis,
                    name: kode.nama_jenis,
                  };
                  return data;
                })}
              />
            ) : (
              <Skeleton width={"100%"} height={50} />
            )}
          </div>
          <div className="col-md-4 col-lg-4">
            <Field
              name="kode_barocde"
              label="Kode Barcode"
              type="text"
              component={renderField}
              placeholder="Biarkan Kosong jika Tidak Digunakan"
              normalize={(value) => value && value.toUpperCase()}
              onChange={this.handleBarcode()}
            />
          </div>
          <div
            className={
              this.state.aksesoris !== "col-lg-12 row"
                ? "col-md-3 col-lg-3 d-none"
                : "col-md-3 col-lg-3 d-none"
            }
          >
            <Field
              name="kadar"
              label="Kode Tukar"
              type="number"
              component={renderField}
            />
          </div>
          <div className="col-md-3 col-lg-3 d-none">
            <Field
              name="kodeKategori"
              label="Kode Tukar"
              type="number"
              component={renderField}
            />
          </div>

          <div className={this.state.non_aksesoris}>
            <div className="col-md-12 col-lg-12">
              <Field
                name="keterangan"
                label="Keterangan"
                type="text"
                component={renderField}
                normalize={(data) => data?.toUpperCase()}
              />
            </div>
            <div className="col-md-3 col-lg-3 d-none">
              <Field
                name="namaKategori"
                label="Kode Tukar"
                type="number"
                component={renderField}
              />
            </div>
            <div className="col-md-3 col-lg-3">
              <Field
                name="p"
                label="Plastik"
                type="number"
                component={renderField}
              />
            </div>
            <div className="col-md-3 col-lg-3">
              <Field
                name="k"
                label="Kertas"
                type="number"
                component={renderField}
              />
            </div>
            <div className="col-md-3 col-lg-3">
              <Field
                name="pkg"
                label="Pkg (Gr)"
                type="number"
                component={renderField}
                onBlur={() => this.onclick1()}
              />
            </div>
            <div className="col-md-3 col-lg-3">
              <Field
                name="bruto"
                label="Berat (Gr)"
                type="number"
                component={renderField}
                onChange={this.onclick1()}
                // onChange={this.setBruto()}
                // onBlur={this.setBruto()}
              />
            </div>
            <div className="col-md-3 col-lg-3 d-none">
              <Field
                name="kadarTransaksi"
                label="Kode Tukar"
                type="number"
                component={renderField}
                onBlur={() => this.onclick1()}
              />
            </div>
            <div className="col-md-3 col-lg-3 d-none">
              <Field
                name="netto"
                label="Netto (Gr)"
                type="number"
                component={renderField}
                readOnly={true}
              />
            </div>
            <div className="col-md-3 col-lg-3 d-none">
              <Field
                name="gross"
                label="Gross (Gr)"
                type="number"
                component={renderField}
                readOnly={true}
              />
            </div>
            <div className="col-md-3 col-lg-3 d-none">
              <Field
                name="discGlobalPr"
                label="Diskon (%)"
                type="number"
                component={renderField}
                onBlur={() => this.onclick1()}
              />
            </div>
            <div className="col-md-3 col-lg-3 d-none">
              <Field
                name="discGlobalGr"
                label="Diskon (Gr)"
                type="number"
                component={renderField}
                readOnly={true}
              />
            </div>
            <div className="col-md-3 col-lg-3 d-none">
              <Field
                name="nettoDiscGlobalExt"
                label="Netto - Diskon (Gr)"
                type="number"
                component={renderField}
                readOnly={true}
              />
            </div>
            <div className="col-md-3 col-lg-3 d-none">
              <label htmlFor="">Ongkos (Rp)</label>
              <Field
                name="rp"
                component="input"
                type="tel"
                className="form-control"
                {...currencyMask}
              />
            </div>
          </div>
          <div className={this.state.aksesoris}>
            <div className="col-lg-3">
              <Field
                name="qty"
                label="Qty"
                type="number"
                component={renderField}
              />
            </div>
            <div className="col-lg-3 d-none">
              <Field
                name="harga_jual"
                label="Harga Jual"
                type="telp"
                component={renderField}
                {...currencyMask}
              />
            </div>
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button
                type="submit"
                disabled={this.props.onSend}
                className="btn btn-primary"
              >
                {this.props.onSend ? (
                  <span
                    className="spinner-border spinner-border-sm mr-3"
                    aria-hidden="true"
                  ></span>
                ) : null}
                Selesai
                <i className="fa fa-paper-plane ml-3"></i>
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormBarangSet = reduxForm({
  form: "FormBarangSet",
  enableReinitialize: true,
})(FormBarangSet);
const selector = formValueSelector("FormBarangSet"); // <-- same as form name
export default connect((state) => {
  const { bruto, pkg } = selector(state, "bruto", "kadarTransaksi", "pkg");
  return {
    netto: parseFloat(bruto || 0).toFixed(3),
    gross: parseFloat(bruto || 0) + parseFloat(pkg || 0),
    discGlobalGr: 0,
    KodeBarangList: state.provinsi.kodeBarang,
    onSend: state.provinsi.onSend,
    initialValues: {
      harga_jual: 0,
      discGlobalPr: 0,
      p: 0,
      k: 0,
      pkg: 0,
      bruto: 0,
    },
  };
})(FormBarangSet);
